import React, { Component } from "react";
import API from "../../utils/API";
import Card from "../../components/Card";
import Navbar from "../../components/Nav";

class Home extends Component {
    constructor(props)  {
        super(props);
        this.state = {
            articles: []
        }
    }

    componentDidMount() {
        this.loadArticles();
    }

    loadArticles = () => {
        API.scrape
            .then(() => {
                API.getAllArticles
                    .then(articles => {
                        this.setState({articles: articles});
                    });
            });
    }

    render() {
        return (
            <div>
                {/* <Navbar /> */}
                {this.state.articles.map(a => <Card headline={a.headline}>{a.summary}</Card>)}

            </div>
        )
    }
}

export default Home;


